.app {
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  overflow: hidden;
}

.app-single {
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
}

.app-loader {
  position: fixed;
  z-index: 100000;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  pointer-events: none;
  background-color: #ebe8e3;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.app-loader--body {
  width: 200px;
  height: 200px;
  background: url(../../svg/loader.svg)
}


